.sidenav-wrapper {
  position: fixed;
  height: 100%;
  width: 100%;
  z-index: 100;
  visibility: hidden;

  &.transition {
    .sidenav {
      transform: translate(0, 0);
    }

    .fill {
      opacity: 0.4;
    }
  }

  &.visible {
    visibility: visible;
  }

  .sidenav {
    position: absolute;
    z-index: 2;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    overflow: hidden;
    min-width: 240px;
    width: 75%;
    height: 100%;
    background: #FFFFFF;
    transition: all 0.1s linear;
    transform: translate(-230px, 0);
    display: flex;
    flex-direction: column;

    .logo-wrapper {
      display: flex;
      align-items: center;
      border-bottom: 1px solid #e7e7e7;
      flex-shrink: 0;
      height: 78px;
      position: relative;
      overflow: hidden;
      border-bottom-left-radius: 10px;
      border-bottom-right-radius: 10px;

      img {
        height: 30px;
        margin-left: 20px;
        margin-right: 20px;
        transition: 100ms ease transform;

        &:active {
          transform: scale(1.05);
        }
      }

      .logo-text {
        color: #fff;

        .app-name {
          font-size: 16px;
        }

        .app-version {
          font-size: 11px;
        }
      }
    }

    .items {
      flex-grow: 1;
      overflow: auto;

      .items-block {
        padding: 10px 17px;
        border-bottom: 1px solid #e7e7e7;

        .content-item {
          &.active {
            font-weight: bold;
          }

          .list-item-img {
            width: 17px;
            height: 17px;
            margin-right: 17px;
          }
        }
      }
      .items-block:last-of-type {
        border-bottom: none;
      }
    }

    .tags-list-wrapper {
      flex-direction: column;

      .sidenav-tag-wrapper {
        display: flex;
        align-items: center;
        margin-bottom: 5px;

        img {
          height: 17px;
          width: 17px;
          margin-right: 11px;
        }
      }
    }
  }

  .fill {
    position: absolute;
    z-index: 1;
    height: 100%;
    width: 100%;
    background: #000000;
    transition: all 0.1s linear;
    opacity: 0;
  }
}